<template>
	<view class="mainVieW">
		<!-- 主站 -->
		<HOMES ref='home' v-if='active==0'></HOMES>
		<MAKEMONEY ref='make_money' v-if='active==1'></MAKEMONEY>
		<MEMBER ref='member' v-if='active==4'></MEMBER>
		<BUSINESSCARD ref='card' v-if='active==3'></BUSINESSCARD>
		<view class="blank-tabbar" :class="[flagTypeInfo? 'tabbarMainIphone':'']">
		</view>
		<tab-bar class="fixs" ref='commentTabbat' :actives='active'></tab-bar>
	</view>
</template>
<script>
	import MEMBER from '@/pages/tabbar/member.vue'
	import HOMES from '@/pages/tabbar/home.vue'
	import MAKEMONEY from '@/pages/tabbar/make_money.vue'
	import BUSINESSCARD from '@/pages/tabbar/business_card.vue'
	import tabBar from '@/pages/tabbar/tabbar.vue'

	export default {
		name: 'mains',
		data() {
			return {
				active: Number || 0,
				flagTypeInfo: false, // 检测当前是否是IPHONE 异形屏幕
			}
		},
		components: {
			HOMES,
			MEMBER,
			MAKEMONEY,
			BUSINESSCARD,
			tabBar
		},
		onLoad() {
			this.flagTypeInfo = this.checkPlatFromFunc();
			let temp = uni.getStorageSync('setStatusIndexFunc') || 0
			uni.setStorageSync('setStatusIndexFunc', temp)
			this.active = temp
			this.$nextTick(() => {
				this.$refs.commentTabbat.getSetting(temp)
				this._initData();
			})
		},
		// #ifdef MP
		onShareAppMessage(res) {
			return {
				title: '邀您一起体验',
				path:'/pages/index'
			}
		},
		// #endif
		onShow() {
			// 返回悬赏大厅刷新页面
			this.active = uni.getStorageSync('setStatusIndexFunc') || 0;
			this.$nextTick(() => {
				switch (this.active) {
					case 1:
						this.$refs.make_money.loadNewData()
						break;
				}
			})
		},
		onReachBottom() {
			// 首页到底加载下一页
			this.active = uni.getStorageSync('setStatusIndexFunc') || 0;
			this.$nextTick(() => {
				switch (this.active) {
					case 0:
						this.$refs.home.loadNewData()
						break;
					case 1:
						this.$refs.make_money.loadNewData('more')
						break;
				}
			})

		},

		provide() {
			return {
				_mainFuncInit: this._mainFuncInit
			}
		},
		methods: {
			/**
			 * @name 公共点击函数
			 * */
			_mainFuncInit() {
				this.active = uni.getStorageSync('setStatusIndexFunc') || 0
				// console.log(this.active)
				if (this.tempActive == this.active && this.active != 4) {
					return
				}
				this.$nextTick(() => {
					switch (this.active) {
						case 0:
							uni.setStorageSync('setStatusIndexFunc', 0)
							this.$refs.home.getData();
							break;
						case 1:
							uni.setStorageSync('setStatusIndexFunc', 1)
							this.$refs.make_money.getData();
							break;
						case 2:
							break;
						case 3:
							uni.setStorageSync('setStatusIndexFunc', 3)
							// this.$refs.card.getData(); 
							break;
						case 4:
							uni.setStorageSync('setStatusIndexFunc', 4)
							this.$refs.member.getData();
							break;
					}
				})
				this.tempActive = this.active
			},
			/**
			 * @name 首页数据初始化 
			 * */
			_initData() {
				this._mainFuncInit();
			},
			checkPlatFromFunc() {
				let modelmes = uni.getSystemInfoSync().model;
				let tempMoble = Boolean;
				if (modelmes.indexOf('iPhone X') >= 0 || modelmes.indexOf('iPhone XR') >= 0 || modelmes.indexOf('iPhone XS') >= 0 ||
					modelmes.indexOf('iPhone 12') >= 0 || modelmes.indexOf('iPhone 11') >= 0 || modelmes.indexOf('iPhone11') >= 0 ||
					modelmes.indexOf('iPhone12') >= 0 || modelmes.indexOf('iPhoneXR') >= 0 || modelmes.indexOf('iPhoneX') >= 0) {
					tempMoble = true
				} else {
					tempMoble = false
				}

				return tempMoble
			}
		}
	}
</script>
<style scoped>
	.tabbarMainIphone {
		padding-bottom: 64rpx;
	}

	.fixs {
		position: fixed;
		z-index: 100;
	}
</style>
